}

.coinListContainer {
    background-color: white;
    overflow-y: scroll;
    width: 100%;
    max-width: 460px;
    max-height:550px;
    position: absolute;
    top: 60%;
    bottom: 0;
    left: 22%;
    transform: translate(-50%, -50%);
