    -ms-transform: translate(-50%, -50%);
}

.materialContainer {
    width: 100%;
    max-width: 460px;
    max-height: 750px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
}
*, *::before, *::after {
    box-sizing: border-box;
}

